.portalBody {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color: white;
    letter-spacing: 0.5em;
    padding-top: 30px;
    padding-bottom: 0px;
    padding-left: 100px;
    padding-right: 100px;
    overflow: hidden;
}

.headerTime {
    font-weight: lighter;
    font-size: 6em;
}

.timeBlock {
    width: 40%;
}

.headerDate {
    font-weight: lighter;
    font-size: 1.5em;
}

.calendarList {
    margin-top: 50px;
    padding-left: 50px;
    width: 80%;
    height: 500px;
    padding-bottom: 80px;
    overflow: hidden;
}

.calendarItemOld {
    color: gray;
    letter-spacing: normal;
    margin-top: 11px;
    margin-bottom: 11px;
    font-size: 1.3em;
}

.calendarItem {
    letter-spacing: normal;
    margin-top: 11px;
    margin-bottom: 11px;
    font-size: 1.3em;
}

.calendarItemSoon {
    margin-top: -2px;
    margin-bottom: -2px;
    letter-spacing: normal;
    font-size: 1.7em;
}

.calendarItemSooner {
    margin-top: -2px;
    margin-bottom: -2px;
    color: rgb(107, 213, 255);
    letter-spacing: normal;
    font-size: 3em;
}

.twitterScroll {
    position: fixed;
    bottom: 30px;
    height: 70px;
    left: 0px;
    width: 100%;
    height: 100px;
    white-space: nowrap;
    overflow: hidden;
    box-sizing: border-box;
}

.weatherBox {
    position: absolute;
    top: 30px;
    right: 230px;
    width: 600px;
    height: 130px;
    font-size: 1.8em;
    padding: 10px;
    text-align: center;
}

.weatherIcon {
    position: relative;
    top: 10px;
}

.forecastBox {
    margin-top: 12px;
    padding-top: 12px;
    border-top: 1px dotted gray;
}

.forecastItem {
    display: inline-block;
    height: 70px;
    width: 70px;
    letter-spacing: normal;
    font-size: 0.7em;
    padding: 7px;
}

.twitterScroll p {
    vertical-align: middle;
    font-size: 2.8em;
    letter-spacing: normal;
    display: inline-block;
    padding-left: 100%;
    animation: twitterScroll 50s linear infinite;
}

@keyframes twitterScroll {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(-100%, 0);
    }
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.funnyThingContainer {
    z-index: 100;
    position: absolute;
    display: block;
    top: 320px;
    right: 350px;
    width: 500px;
    height: 450px;
    overflow-y: visible;
    /* border: 1px solid blue; */
    padding: 10px;
    /* border-radius: 20px; */
    letter-spacing: normal;
    font-size: 1.7em;
    line-height: 1.5em;
    text-align: center;
    opacity: 1;
    animation: fadeinout 90s linear forwards;
}

@keyframes fadeinout {
    0% {
        opacity: 0;
    }
    2% {
        opacity: 0;
    }
    10% {
        opacity: 1;
    }
    98% {
        opacity: 1;
    }
    100% {
        opacity: 0;
    }
}
